import React, { useState } from 'react';
import { MoonPigCardGallery } from './components/MoonPigCardGallery/MoonPigCardGallery';
import { Provider } from './ioc.react';
import { container } from './ioc';
import { Layout } from 'antd';
import { Product } from './common/Card';
import MoonPigCardDialog from './components/MoonPigCardDialog/MoonPigCardDialog';



/**
 * The Props of the Gallery Item
 */
interface AppState {
  isModalOpen: boolean,
  selectedMoonpigProductNo?: string
}

const App: React.FC = () => {

  const [state, setState] = useState<AppState>({
    isModalOpen: false
  });

  const handleCancel = () => {
    setState({
      ...state,
      isModalOpen: false
    })
  };

  const handleOnClick = (event: React.MouseEvent, product: Product) => {
    setState({
      isModalOpen: true,
      selectedMoonpigProductNo: product.MoonpigProductNo
    })
  }

  return (
    <Layout className="layout">
      <Layout.Header>
      </Layout.Header>
      <Layout.Content style={{ padding: '0 50px' }}>
        <Provider container={container}>
          <MoonPigCardGallery onClick={handleOnClick} />
          <MoonPigCardDialog visible={state.isModalOpen} onCancel={handleCancel} MoonpigProductNo={state.selectedMoonpigProductNo}/>
        </Provider>
      </Layout.Content>
      <Layout.Footer style={{ textAlign: 'center' }}>David Vella Â©2019</Layout.Footer>
    </Layout>
  );
}

export default App;
