import * as React from "react";
import { Modal, Spin, Button, InputNumber } from 'antd';
import { useInjection } from "../../ioc.react";
import { IMoonPigClient } from "../../common/IMoonPigClient";
import { useState } from "react";
import { ICardInformation } from "../../common/Card";
import GalleryItem from "../GalleryItem/GalleryItem";
import Radium from "radium";
import Title from "antd/lib/typography/Title";


/**
 * The Props of the Dialog 
 */
interface MoonPigDialogProps {
    /**
     * The MoonPigProductNo
     */
    MoonpigProductNo?: string
    /**
     * Whether the dialog is visible or not.
     */
    visible?: boolean;
    /**
     * Whether the dialog is fetching data
     */
    isLoading?: boolean;
    /**
     * Action when user closes the dialog
     */
    onCancel?: (e: React.MouseEvent<HTMLElement>) => void;
}

/**
 * The Props of the Gallery Item
 */
interface MoonPigDialogState {
    isLoading: boolean,
    card: ICardInformation
}

const dialogStyles = {
    dialogGrid: {
        display: 'flex',
        flexFlow: 'row wrap',
        margin: '0 0',
        alignItems: 'center',
        justifyContent: 'center'
    },
    dialogGridItem: {
        margin: 10
    },
    optionButton: {
        width: '100%',
        height: 76,
        marginTop: 10
    }
};

/**
 * Item in the gallery
 */
const MoonPigCardDialog: React.FC<MoonPigDialogProps> = props => {

    const [state, setState] = useState<MoonPigDialogState>({
        isLoading: false,
        card: {} as ICardInformation
    });

    const client = useInjection<IMoonPigClient>('moonPigClient');

    // Similar to componentDidMount and componentDidUpdate:
    React.useEffect(() => {
        const setCardData = () => {
            // Get Cards from MoonPig
            setState({
                ...state,
                isLoading: true
            });

            if (props.MoonpigProductNo) {
                client.Products.getCard(props.MoonpigProductNo)
                    .then(result => {
                        setState({
                            ...state,
                            isLoading: false,
                            card: result
                        });
                    })
                    .catch(error => {
                        global.console.log(error);
                        setState({
                            ...state,
                            isLoading: false
                        });
                    })
            }
        };

        setCardData();
        // eslint-disable-next-line
    }, [props.MoonpigProductNo]);

    return (
        <div>
            <Modal
                visible={props.visible}
                onCancel={props.onCancel}
                footer={null}
                width={900}
            >
                {state.isLoading ? (
                    <div >
                        <div style={{ textAlign: 'center', marginTop: 20 }}>
                            <Spin tip="Loading..." size="large" />
                        </div>
                    </div>
                ) : (
                        <div style={dialogStyles.dialogGrid}>
                            <div style={dialogStyles.dialogGridItem}>
                                <GalleryItem Title={state.card.Title}
                                    ImageUrl={state.card.ThumbnailUrl}
                                    ImageAlt={state.card.Title}></GalleryItem>
                            </div>
                            <div style={dialogStyles.dialogGridItem}>
                                <table>
                                    <tr>
                                        <Title level={3}>{state.card.Title}</Title>
                                    </tr>
                                    <tr>
                                        <Button style={dialogStyles.optionButton}>Standard Card</Button>
                                    </tr>
                                    <tr>
                                        <Button style={dialogStyles.optionButton}>Large Card</Button>
                                    </tr>
                                    <tr>
                                        <Button style={dialogStyles.optionButton}>Card Pack</Button>
                                    </tr>
                                    <tr>
                                        <Button style={dialogStyles.optionButton}>Giant Card</Button>
                                    </tr>
                                    <tr>
                                        <div style={dialogStyles.optionButton}>
                                            Quantity: <InputNumber min={1} max={10} defaultValue={1} />
                                        </div>
                                    </tr>
                                    <tr>
                                        <Button style={dialogStyles.optionButton} type="primary">Buy Me</Button>
                                    </tr>
                                </table>
                            </div>

                        </div>
                    )}
            </Modal>
        </div>
    );
};

export default Radium(MoonPigCardDialog);