import Axios, { AxiosInstance } from "axios";
import { Constants } from "./Constants";
import { IMoonPigClient } from "./IMoonPigClient";
import { ProductClient } from "./ProductClient";
import { IProductsClient } from "./IProductsClient";
import { injectable } from "inversify";

/**
 * Class used to make Rest Calls to the moon pig web api and to be faked for testing purposes.
 * In real world I would flesh this out to be used by everyone and for test environments where
 * applicable.
 */
@injectable()
export class MoonPig implements IMoonPigClient {
  /**
   * Search Url
   */
  private static readonly MOON_PIG_SEARCH_URL =
    "https://search.moonpig.com/api";

  /**
   * A lightweight REST and HTTP client optimized for use with TypeScript with generics and async await.
   */
  _client: AxiosInstance;

  constructor() {
    this._client = Axios.create({
      baseURL: Constants.GET_CORS_ANYWHERE + MoonPig.MOON_PIG_SEARCH_URL
    });
  }

  /**
   * The Product Client
   */
  public get Products(): IProductsClient {
    return new ProductClient(this._client);
  }
}
