import { AxiosInstance } from "axios";
import { IProductsClient } from "./IProductsClient";
import { ICardCollection, ICardInformation } from "./Card";
import Axios from "axios";

export class ProductClient implements IProductsClient {
  /**
   * A lightweight REST and HTTP client optimized for use with TypeScript with generics and async await.
   */
  _client: AxiosInstance;

  constructor(client: AxiosInstance) {
    this._client = client;
  }

  /**
   * Get Cards from the gallery
   */
  public async getCards(numberOfCards: number): Promise<ICardCollection> {
    return new Promise<ICardCollection>(async (resolve, reject) => {
      this._client
        .get<ICardCollection>(
          `/products?size=${numberOfCards}&fq=card_shop_id:1`
        )
        .then(res => {
          resolve(res.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  /**
   * Get Card from the gallery
   */
  public getCard(moonpigProductNo: string): Promise<ICardInformation> {
    return new Promise<ICardInformation>(async (resolve, reject) => {
      Axios.get<ICardInformation>(
          `https://cors-anywhere.herokuapp.com/https://www.moonpig.com/uk/api/product/product/?mpn=${moonpigProductNo}`
        )
        .then(res => {
          resolve(res.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
}
