import * as React from "react";
import Radium from 'radium';
import { Card } from 'antd';

const galleryItemStyles = {
  galleryItem: {
    /* Margin value should be half of grid-gap value as margins on flex items don't collapse */
    display: 'grid',
    overflow: 'hidden',
    alignItems:'center',
    justifyItems:'center',
    width:'273.59px',
    maxHeight:'388.07px'
  },  
  unstyledButton: {
    backgroundColor: 'Transparent',
    backgroundRepeat:'no-repeat',
    border: 'none',
    cursor:'pointer',
    overflow: 'hidden',
    outline:'none',
    transition: 'all .3s ease-in-out',
    ':hover': {
      MozBoxShadow: 'rgba(0, 0, 0, 0.22) 0px 19px 43px',
      WebkitBoxShadow: 'rgba(0, 0, 0, 0.22) 0px 19px 43px',
      boxShadow: 'rgba(0, 0, 0, 0.22) 0px 19px 43px',
      transform: 'translate3d(0px, -1px, 0px)'
    }
  }
};

/**
 * The Props of the Gallery Item
 */
interface GalleryItemProps {
  /**
   * The Title text of the Gallery Item
   */
  Title: string
  /**
   * The URL (web address) of the image.
   */
  ImageUrl: string
  /**
   * The alt attribute provides alternative information for an image if a user for some reason cannot view it.
   */
  ImageAlt: string
  /**
   * Execute a JavaScript when a button is clicked.
   */
  onClick?: (event: React.MouseEvent) => void
  /**
   * The onkeypress event occurs when the user presses a key (on the keyboard).
   */
  onKeyPress?: (event: React.KeyboardEvent) => void
}

/**
 * Item in the gallery
 */
const GalleryItem: React.FC<GalleryItemProps> =
  ({ Title, ImageUrl, onClick, onKeyPress, ImageAlt }) => (
    <div>
      <button style={galleryItemStyles.unstyledButton} onKeyPress={onKeyPress} title={Title}>
        <Card
          hoverable
          cover={<img alt={ImageAlt} src={ImageUrl} />}
          onClick={onClick}
          style={galleryItemStyles.galleryItem}
          bodyStyle={{
            display:"none"
          }}
        >
        </Card>
      </button>
    </div>
  )

export default Radium(GalleryItem);