import * as React from "react";
import { Product } from "../../common/Card";
import GalleryItem from "../GalleryItem/GalleryItem";

/**
 * The Props of the Gallery Item
 */
interface MoonPigGalleryItemProps {
    /**
     * MoonPig Product
     */
    Product: Product
    /**
     * Execute a JavaScript when a button is clicked.
     */
    onClick?: (event: React.MouseEvent, product: Product) => void
    /**
     * The onkeypress event occurs when the user presses a key (on the keyboard).
     */
    onKeyPress?: (event: React.KeyboardEvent, product: Product) => void
}

/**
 * A card in the gallery
 */
export const MoonPigGalleryItem: React.FC<MoonPigGalleryItemProps> = (props) => {

    const handleOnClick = (event: React.MouseEvent) => {
        if (props.onClick) {
            props.onClick(event, props.Product);
        }
    }

    const handleOnKeyPress = (event: React.KeyboardEvent) => {
        if (props.onKeyPress) {
            props.onKeyPress(event, props.Product);
        }
    }

    return (
        <div>
            <GalleryItem Title={props.Product.Title}
                ImageUrl={props.Product.ProductImage.Link.Href}
                ImageAlt={props.Product.Title}
                onClick={handleOnClick}
                onKeyPress={handleOnKeyPress}></GalleryItem>
        </div>
    );
};
